	border-top-right-radius: 0;
	border-bottom-right-radius: 0;
}

.em-booking-graph-pending {
	border-radius: 999px;
	position: relative;
	height: 8px;
	background: #ffc107;
}

.em-booking-graph-pending.cut {
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
}

.em-booking-graph-full {
	border-radius: 999px;
	position: relative;
	height: 8px;
	background: #f55e53;
}

